import { Button, Drawer, Form, Input, Radio, Space, Upload, message } from "antd";
import { CloseOutlined, PlusOutlined } from "@ant-design/icons";
import { addApp } from "@/api/modules/app";

interface Props {
	open: boolean;
	onSuccess: () => void;
	onClose: () => void
}

const CreateAppPage = (props: Props) => {
	const [form] = Form.useForm();

	const onFinish = async (values: any) => {
		try {
			await addApp({
				name: values.name,
				type: values.type
			});
			//清空form
			form.resetFields();
			message.success("创建成功");
			props.onSuccess();
		} catch (error) {
			console.log(error);
		}
	};

	const normFile = (e: any) => {
		if (Array.isArray(e)) {
			return e;
		}
		return e?.fileList;
	};
	return (
		<Drawer
			title="新建应用"
			placement='right'
			width={500}
			closable={false}
			open={props.open}
			extra={
				<Space>
					<CloseOutlined onClick={props.onClose} />
				</Space>
			}
		>
			<Form
				labelCol={{ span: 6 }}
				wrapperCol={{ span: 18 }}
				layout="horizontal"
				form={form}
				onFinish={onFinish}
				initialValues={{ type: 1 }}
			>
				<Form.Item label="应用类型" name="type">
					<Radio.Group>
						<Radio value={1}>H5/PC/Web项目</Radio>
						<Radio value={2}>小程序</Radio>
					</Radio.Group>
				</Form.Item>
				<Form.Item label="应用名称" name="name" rules={[{ required: true }]}>
					<Input />
				</Form.Item>
				<Form.Item label="上传图标" valuePropName="fileList" getValueFromEvent={normFile}>
					<Upload action="/upload.do" listType="picture-card">
						<div>
							<PlusOutlined />
							<div style={{ marginTop: 8 }}>Upload</div>
						</div>
					</Upload>
				</Form.Item>
				<Form.Item wrapperCol={{ offset: 6, span: 18 }}>
					<Button type="primary" htmlType="submit">
						确定
					</Button>
				</Form.Item>
			</Form>
		</Drawer>
	);
};
export default CreateAppPage;