import Layout from "antd/es/layout/layout";
import { Button, Card, Col, Row, Typography, Image, Dropdown, MenuProps, Empty, message, Pagination, PaginationProps } from 'antd';
import { useEffect, useState } from "react";
import "./app.less";
import { CopyOutlined, DeleteOutlined, EditOutlined, PlusOutlined, SettingOutlined } from "@ant-design/icons";
import { getAppList } from "@/api/modules/app";
import H5Logo from "@/assets/svg/H5.svg"
import MPLogo from "@/assets/svg/mp.svg"
import { useNavigate } from "react-router-dom";
import copy from 'copy-to-clipboard';
import CreateAppPage from "./components/createAppPage";

const { Title, Text } = Typography;

const View: React.FC = () => {
    const navigate = useNavigate();
    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [appTotal, setAppTotal] = useState(0);
    const [appList, setAppList] = useState([]);
    const [showCreatePage, setShowCreatePage] = useState(false);

    const initAppList = async () => {
        const result = await getAppList({ pageNum, pageSize });
        if (result.data) {
            setAppTotal(result.data.total);
            setAppList(result.data.data as []);
        }
    };

    useEffect(() => {
        initAppList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initAppList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const items: MenuProps['items'] = [
        {
            key: '1',
            label: '编辑',
            icon: <EditOutlined />,
            onClick: () => { }
        },
        {
            key: '2',
            label: '删除',
            icon: <DeleteOutlined />,
            onClick: () => { }
        },
    ];

    const navigateToApp = () => {
        navigate('/home/web');
    }

    const copyText = (e: React.MouseEvent, text: string) => {
        e.stopPropagation();
        copy(text);
        message.success('已复制')
    }

    const onChange: PaginationProps['onChange'] = (page, pageSize) => {
        setPageNum(page);
        setPageSize(pageSize);
    };

    return <Layout>
        <div style={{ margin: '0 32px', padding: '16px' }}>
            <Title level={4}>应用接入</Title>
            <div className="app-header">
                <Button type="primary" icon={<PlusOutlined />} size="middle" onClick={e => setShowCreatePage(true)}>
                    创建应用
                </Button>
            </div>
            <div className="app-list">
                {appList.length > 0 ? <div>
                    <Row gutter={[16, 16]}>
                        {
                            appList.map((app: any, index) => {
                                return (<Col key={index.toString()} span={24 / 4}>
                                    <Card hoverable>
                                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
                                            <div style={{ display: 'flex', flexDirection: 'row' }}>
                                                <Image
                                                    width={20}
                                                    height={20}
                                                    src={app.type === '1' ? H5Logo : MPLogo}
                                                />
                                                <Title style={{ marginLeft: '8px' }} level={5}>{app.name}</Title>
                                            </div>
                                            <Dropdown menu={{ items }} placement="bottomRight" arrow={{ pointAtCenter: true }} >
                                                <SettingOutlined />
                                            </Dropdown>
                                        </div>
                                        <div style={{ marginTop: '16px' }}>
                                            <Text>AppID： <Text type="secondary">{app.aid}</Text><CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, app.aid)} /></Text>
                                        </div>
                                        <div style={{ marginTop: '8px' }}>
                                            <Button type="text" onClick={navigateToApp}>点击进入</Button>
                                        </div>
                                    </Card>
                                </Col>)
                            })
                        }
                    </Row>
                    <Pagination
                        style={{ marginTop: '20px' }}
                        showSizeChanger
                        onChange={onChange}
                        defaultCurrent={pageNum}
                        total={appTotal}
                    />
                </div> : <Empty description="暂无应用" />}
            </div>
        </div>
        <CreateAppPage onSuccess={() => { setShowCreatePage(false); initAppList(); }} open={showCreatePage} onClose={() => setShowCreatePage(false)}></CreateAppPage>
    </Layout>;
}

export default View;